import React, { useEffect, useState } from "react";
import './miniMapViewStyle.scss';
import { debounce } from "lodash";
import Minimap from '../minimap/minimap';

const MinimapSection = ({ contentToHighlight }) => {
  const [isScrolling, setScrolling] = useState(false);

  const scrollListener = () => {
    console.log('scrolling start')
    setScrolling(!isScrolling);
  }

  const scrollStop = () => {
      console.log('scrolling stopped')
      setScrolling(false);
  }

  useEffect(() => {
    window.addEventListener('scroll', function() {
        scrollListener();
    }, false);

    window.addEventListener('scroll', debounce(() => { scrollStop() }, 4000))
    //return () => window.removeEventListener("scroll", scrollListener); //
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  return (
    <div>
      { isScrolling &&
        <div className="minimap-container">
          <Minimap 
              rootId="root"
              position="right" 
              smoothScroll={true} 
              smoothScrollDelay={200} 
              toggle={true}
              contentToHighlight={contentToHighlight}
          />
        </div>
      }
    </div>
  );
};

export default MinimapSection;
